import * as React from 'react';
import Modal from '../common/Modal';
import Button from '../common/Button';

export interface ILeavePageProps {
    show: boolean,
    onSaveAndContinue: any,
    onContinueWithoutSaving: any,
    onCancel: any,
    showWithoutSavingButton?: boolean,
}

interface ILeavePageState {
    SaveAndContinue: boolean;
}

class LeavePage extends React.Component<ILeavePageProps, ILeavePageState> {
    constructor() {
        super();
        this.state = {
            SaveAndContinue: false
        };

        this.onSaveAndContinue = this.onSaveAndContinue.bind(this);
    }

    onSaveAndContinue() {
        this.setState({ SaveAndContinue: true });

        this.props.onSaveAndContinue();
    }

    componentWillReceiveProps(nextProps: ILeavePageProps) {
        if (!nextProps.show)
            this.setState({ SaveAndContinue: false });
    }

    render() {
        return (
            <Modal show={this.props.show}>
                <Modal.Header>
                    <div className="description">Leaving?</div>
                </Modal.Header>
                <Modal.Body>
                    Unsaved changes have been made.
                </Modal.Body>
                <Modal.Footer>
                    <Button onClick={this.onSaveAndContinue} loading={this.state.SaveAndContinue} className="btn">Save and Continue</Button>
                    {this.props.showWithoutSavingButton == null || this.props.showWithoutSavingButton ? <Button onClick={this.props.onContinueWithoutSaving} disabled={this.state.SaveAndContinue} className="btn">Discard and Continue</Button> : null }
                    <Button onClick={this.props.onCancel} disabled={this.state.SaveAndContinue} className="btn">Return</Button>
                </Modal.Footer>
            </Modal>
        );
    }
}

export default LeavePage;


